




















































































import Vue from 'vue';
import Component from 'vue-class-component';

import UsersToolBar from '@/components/UsersToolBar.vue'
import { User } from '@/store/types';

import axios from 'axios';

@Component({
  components: {
    UsersToolBar
  }
})

export default class UsersList extends Vue {
  public isLoading: boolean = false;
  public users: Partial<User>[] = [];
  public user: Partial<User> = {};
  public submitted: boolean = false;
  public userDialog: boolean = false;
  public selectedRoles: string = [].join();
  public selectedUsers: Partial<User> = {};
  public selectedUsesValue: number = 0;
  public menuModel: Array<object> = [
                {
                  label: `Reset password for ${this.selectedUsesValue} users`,
                  icon: 'pi-key',
                  command: () => this.viewUser(this.selectedUsers)
                },
                {
                  label: `Disable ${this.selectedUsesValue} users`,
                  icon: 'pi pi-fw pi-times',
                  command: () => this.deleteUser(this.selectedUsers)
                },
                {
                  label: `Delete ${this.selectedUsesValue} users`,
                  icon: 'pi pi-fw pi-times',
                  command: () => this.deleteUser(this.selectedUsers)
                },
            ];
    
  public roles: Array<string> = ['User', 'Admin', 'Modeler'];
  public filters: object = {};
  public buttonOptions: Array<object> = [{
      label: 'No options yet...',
      icon: 'pipi-question',
      command: () => {
        this.$toast.add({severity:'success', summary:'Updated', detail:'Data Updated', life: 3000});
      }
        },];

  openNew() {
    this.user = {};
    this.submitted = false;
    this.userDialog = true;
  }

  hideDialog() {
    this.userDialog = false;
    this.submitted = false;
  }

  saveUser() {
    this.submitted = true;
    // eslint-disable
    if (this.user!.name!.trim()) {
        if (this.user.id) {
          console.log(this.user)
            this.$set(this.users, this.findIndexById(`${this.user.id}`), {
              ...this.user,
              role: this.user.roles!.join('') as string,
            });
            this.$toast.add({severity:'success', summary: 'Successful', detail: 'User Updated', life: 3000});
        }
        else {
            this.user.id = Number(this.createId());
            this.users.push({
              ...this.user,
              id: Number(this.user.id),
              role: this.user.roles!.join(', ')
            });
            this.$toast.add({severity:'success', summary: 'Successful', detail: 'User Created', life: 3000});
        }

        this.userDialog = false;
        this.user = {};
    }
  }

  findIndexById(id: string) {
    let index = -1;
    for (let i = 0; i < this.users.length; i++) {
        if (this.users[i].id === +id) {
            index = i;
            break;
        }
    }

    return index;
  }

  onRowContextMenu(event: any) {
    (this.$refs.cm as Vue & {show: () => boolean }).show();
  }

  viewUser(user: Partial<User>) {
    this.$toast.add({severity: 'info', summary: 'User Selected', detail: user.name});
  }

  deleteUser(user: Partial<User>) {
      this.users = this.users.filter((user) => user.id !== user.id);
      this.$toast.add({severity: 'info', summary: 'User Deleted', detail: user.name});
      this.selectedUsers = {};
  }

  createId() {
    let id = '';
    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    for ( var i = 0; i < 5; i++ ) {
        id += chars.charAt(Math.floor(Math.random() * chars.length));
    }
    return id;
  }

  created() {
    this.isLoading = true;
    axios.get('/api/users')
      .then(response => this.users = response.data.users)
    this.isLoading = false;
  };
}
